.main-page {
  box-sizing: border-box;
  background-color: #ffffff;
  max-width: 1440px;
  position: relative;
  z-index: 1;
  margin: 0 auto;
  font-family: 'TT Lakes'; }
  @media (min-width: 1336px) {
    .main-page {
      display: grid;
      grid-template-columns: 21.25% 78.75%;
      box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 1px 0px 16px rgba(14, 24, 80, 0.2); } }

.main-page--blur {
  opacity: 0.03;
  transition: all 0.2s; }

.main {
  background-color: #f8f8f8; }

.container-burger {
  height: 100vh;
  min-width: 306px;
  max-width: 360px;
  display: flex;
  flex-direction: column;
  align-content: space-between; }

.container-burger--hidden {
  display: none; }
  @media (min-width: 1336px) {
    .container-burger--hidden {
      display: flex;
      transition: all 0.2s; } }

.container-burger--absolute {
  position: absolute;
  top: 0;
